// set default
a {
    color: $white-cl;
    text-decoration: none;
}

// Heading
.heading {
    font-family: i;

    &.--bold {
        font-family: ib;
    }

    &.--h1 {
        font-size: $fs-h1;
        line-height: $lh-h1;
    }

    &.--h2 {
        font-size: 12rem;
        line-height: 1.2em;
    }

    &.--h3 {
        font-size: 7rem;
        line-height: 1.2em;
    }

    &.--h5 {
        font-size: 2rem;
        line-height: 2.8rem;

        &.--medium {
            font-family: pm;
        }
    }
}

// Body text
.txt {
    font-family: il;

    &.--bold {
        font-family: isb;
    }

    &.--t12 {
        font-size: 1.2rem;
        line-height: 2em;
    }

    &.--t14 {
        font-size: 1.4rem;
        line-height: 2.2rem;
    }

    &.--t16 {
        font-size: 1.6rem;
        line-height: 3rem;
    }
}

.txt-iskry {
    font-family: iskryr;
}



//Hover
.hover-un {
    position: relative;
    @include transition(color $t);

    &:after {
        content: "";
        width: 100%;
        height: .1rem;
        background: $white-cl;
        position: absolute;
        bottom: -.2rem;
        left: 0;
        mix-blend-mode: difference;
        @include transform(scaleX(0));
        @include transform-origin(right);
        @include transition($t transform $cubic);
    }

    &:hover,
    &.active {
        &:after {
            @include transform(scaleX(1));
            @include transform-origin(left);
            @include transition($t transform $cubic);
        }
    }

    &.un-sm {
        &::after {
            bottom: 0;
        }
    }
}

.hover-arr {
    display: inline;
    align-items: center;

    .arrows {
        position: relative;
        margin-left: .4rem;
        display: inline-block;
        overflow: hidden;
        @include size(1.2rem);

        .arrow {
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            transform: translate3d(0%, 0%, 0);
            @include transition(transform 0s);

            &.clone {
                position: absolute;
                top: 0;
                left: 0;
                transform: translate3d(-150%, 150%, 0);
            }
        }
    }

    &:hover {
        .arrows {
            .arrow {
                transform: translate3d(150%, -150%, 0);
                @include transition(transform $cubic $t);

                &.clone {
                    transform: translate3d(0%, 0%, 0);
                }
            }
        }
    }
}