.sc-home-hero {
    height: 100vh;
    position: relative;
    z-index: 1;
    @include transition(opacity $t ease);
    margin-bottom: 50vh;

    .home-hero-grad {
        pointer-events: none;
        position: absolute;
        bottom: -50%;
        left: 0;
        @include size(100%, 50vh);
        background: linear-gradient(180deg, rgba(14, 14, 14, 0) 0%, #0E0E0E 65%);
    }

    &.hide {
        opacity: 0;
    }

    .container {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        .marquee {
            margin-bottom: 8.7rem;

            .line {
                @include size(100%, .1rem);
                background-color: $white10-cl;
            }

            .marquee-wrap {
                display: flex;
                position: relative;
                width: 100%;
                flex-wrap: nowrap;

                // fuild inside container
                width: 100vw;
                margin-left: -50vw;
                left: 50%;

            }

            .marquee-txt {
                @include text(29rem, ps, 18.2rem, transparent, none);
                white-space: nowrap;
                margin-top: 7rem;
                margin-bottom: 11.3rem;
                -webkit-text-stroke-width: .1rem;
                -webkit-text-stroke-color: $white10-cl;

                &::after {
                    content: "\00a0";
                    /* this is &nbsp; */
                }
            }
        }

        .content {
            margin-bottom: 8.7rem;

            .hithere,
            .intro {
                grid-row: 1/2;
            }

            .hithere {
                grid-column: 1/3;
            }

            .intro {
                grid-column: 3/7;
                display: flex;
                flex-direction: column;
                align-items: start;

                .--t14 {
                    margin-bottom: 1.7rem;
                    color: $white30-cl;
                }

                .--h5.--medium {
                    margin-bottom: 1.7rem;
                    text-transform: uppercase;
                }

                .hover-arr {
                    .--h5 {
                        color: $white30-cl;
                        @include transition(color cubic-bezier(0.4, 0, 0.1, 1) $t);
                        display: inline;
                    }

                    .arrows {
                        opacity: .3;
                    }

                    &:hover {
                        color: $white-cl;
                        opacity: 1;

                        .--h5 {
                            color: $white-cl;
                        }

                        .arrows {
                            opacity: 1;
                        }
                    }
                }
            }

            .address {
                grid-column: 11/17;
                @include col(2, 3.6rem);

                .add-item {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;

                    .--t14 {
                        color: $white30-cl;
                        margin-bottom: 2.8rem;
                    }

                    .add-link {
                        margin-bottom: 2.8rem;

                        .--h5 {
                            display: inline;
                            text-transform: uppercase;
                            overflow: hidden;
                        }
                    }
                    .hover-arr {
                        .--h5 {
                            color: $white30-cl;
                            @include transition(color cubic-bezier(0.4, 0, 0.1, 1) $t);
                            display: inline;
                        }
    
                        .arrows {
                            opacity: .3;
                        }
    
                        &:hover {
                            color: $white-cl;
                            opacity: 1;
    
                            .--h5 {
                                color: $white-cl;
                            }
    
                            .arrows {
                                opacity: 1;
                            }
                        }
                    }

                    .tel-link {
                        color: $white50-cl;

                        &:hover {
                            color: $white-cl;
                        }
                    }

                    &:first-child {
                        .add-link {
                            max-width: 23.6rem;
                        }
                    }

                    &:last-child {
                        .add-link {
                            max-width: 29.9rem;
                        }
                    }
                }
            }
        }
    }
}

.sc-home-about {
    position: relative;
    margin-top: -150vh;
    z-index: 5;
    pointer-events: none;
    margin-bottom: -50vh;

    .grid-content {
        position: sticky;
        pointer-events: none;
        top: 0;
        left: 0;
        z-index: 2;
        @include size(100%, 100vh);
        display: flex;
        flex-direction: column;
        justify-content: center;

        .content-inner {
            @include col(1, 0px);

            .content-item {
                text-align: center;
                grid-column: 1/1;
                grid-row: 1/1;
                align-self: center;
                opacity: 0;

                // &:last-child {
                //     opacity: 1;
                // }

                .--h3 {
                    font-family: ps;
                    text-transform: uppercase;

                    .video-wrap {
                        @include size(25.8rem, 8.2rem);
                        background-color: gray;
                        @include br(8.2rem);
                        margin: 0 auto;
                        overflow: hidden;
                        margin-top: 1rem;
                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            display: block;
                        }
                    }

                    .txt-iskry {
                        font-family: iskryr;
                        text-transform: lowercase;
                        color: $white30-cl;

                        .char {
                            opacity: 1 !important;
                        }
                    }
                }
            }
        }
    }

    .grid-imgs {
        padding-top: 150vh;
        width: 100%;
        @include col(8, 0px);
        position: relative;
        z-index: 1;
        padding-bottom: 100vh;
        margin-top: -50vh;

        .grid-item {
            position: relative;
            will-change: transform;
            grid-column: var(--c);
            grid-row: var(--r);

            .grid-item-img {
                @include size(100%, auto);
                will-change: transform, opacity;
                -webkit-aspect-ratio: 1;
                aspect-ratio: 1;
                background-size: contain;
                background-repeat: no-repeat;
                background-origin: 50%;
                background-position: center center;
                position: relative;
            }
        }
    }

    .sub-content {
        position: absolute;
        left: 0;
        bottom: calc(50vh + 6rem);
        width: 100%;

        .--t14 {
            grid-column: 12/17;
            grid-row: 1/2;
            margin-bottom: 3.6rem;
        }

        .--t12 {
            grid-column: 12/15;
            grid-row: 2/3;
            max-width: 24rem;
            color: $white30-cl;
        }

        .--t12:last-child {
            grid-column: 15/17;
        }
    }
}

.sc-home-proj {
    position: relative;
    z-index: 5;
    //margin-top: 145vh;
    margin-top: 100vh;

    .container {
        .title-wrap {
            display: flex;
            justify-content: space-between;
            grid-column: 1/17;
            margin-bottom: 8rem;
            //@include transition(all $t ease);
            //margin-bottom: 40vh;

            & * {
                pointer-events: none;
            }

            .proj-label {
                width: 50%;
                margin-right: 4rem;
                max-width: calc(50% - 4rem);
            }

            .proj-title {
                max-width: 145.3rem;
                width: 50%;
            }

            &.end-state {
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
                grid-column: 2/16;

                .proj-label {
                    width: 100%;
                    max-width: 39.5rem;
                    margin-bottom: 5rem;
                }

                .proj-title {
                    width: 100%;
                    max-width: 145rem;
                }
            }
        }

        .content-wrap {
            grid-column: 2/16;

            .proj-item-wrap {
                perspective: 400px;
                perspective-origin: 50% 0%;

                &:last-child .proj-item {
                    border-bottom: .1rem solid $white10-cl;
                }
            }

            .proj-item {
                @include col(14, 3.6rem);
                padding-top: 5rem;
                padding-bottom: 10rem;
                border-top: .1rem solid $white10-cl;
                position: relative;
                will-change: transform;

                .proj-img {
                    grid-column: 1/2;
                    grid-row: 1/3;
                    height: 100%;
                    pointer-events: none;

                    .proj-img-inner {
                        position: absolute;
                        @include size(34.5rem, 22rem);
                        left: 0;
                        top: 50%;
                        overflow: hidden;
                        opacity: 0;
                        @include transform-origin(50% 50%);
                        @include transform(translate(-100%, -50%));
                        @include transition(all $t ease-in-out);

                        .img-full {
                            @include midimg(100%);
                        }
                    }
                }

                .proj-no {
                    grid-column: 1/15;
                    grid-row: 1/2;
                    color: $white30-cl;
                    margin-bottom: 1.6rem;
                    @include transition(transform $t ease-in-out);
                }

                .proj-title-wrap {
                    grid-column: 1/12;
                    grid-row: 2/3;
                    @include transition(transform $t ease-in-out);
                    position: relative;
                    transform-style: preserve-3d;
                    @include transform-origin(0 0 0);
                    justify-self: flex-start;
                    padding-right: 1rem;

                    .real {
                        text-transform: uppercase;
                        color: $white30-cl;
                        font-size: 10rem;
                        @include transition(color $t ease);

                        .char {
                            opacity: 1;
                            @include transform-origin(0 0 0);
                        }
                    }

                    .clone {
                        position: absolute;
                        top: 0;
                        left: 0;
                        @include size(100%);
                        font-family: iskryr;
                        font-size: 10.46rem;
                        text-transform: uppercase;
                        @include transform-origin(0 0 0);
                        //transform: rotateX(90deg) translateY(109%);

                        .char {
                            //top: .5rem !important;
                            @include transform-origin(0 100% 0);
                            //@include transform-origin(0% 100%);
                            //@include transform(rotateX(90deg) translateY(109%))
                        }
                    }
                }

                .proj-info {
                    grid-column: 12/14;
                    grid-row: 2/3;
                    margin-top: 2.4rem;
                    color: $white30-cl;
                    font-family: im;
                    text-transform: uppercase;
                    letter-spacing: 2%;
                    @include transition(color $t ease);
                }

                .hover-arr {
                    grid-column: 14/15;
                    grid-row: 2/3;
                    @include size(2.2rem);
                    justify-self: flex-end;
                    margin-top: 1.8rem;

                    .arrows {
                        opacity: 0.3;
                        @include size(2.2rem);
                        @include transition(opacity $t ease);
                        margin-left: 0;

                        .arrow {
                            width: 100%;
                        }
                    }
                }

                &:hover {
                    // .proj-img {
                    //     .proj-img-inner {
                    //         opacity: 1;
                    //         @include transform(translate(-10rem, -50%));
                    //     }
                    // }

                    .proj-no {
                        //@include transform(translateX(30rem));
                    }

                    .proj-title-wrap {
                        //@include transform(translateX(30rem));
                    }

                    .arrows {
                        opacity: 1;
                    }
                }
            }
        }
    }
}

.sc-home-hero {
    .heading.--h5 {
        .line {
            overflow: hidden;
        }
    }
}