bp-loading {
    z-index: 999;
    @include fullscreen(100%);
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;

    .loading-cover {
        background-color: $bg-cl;
        @include fullscreen(100%);
        z-index: 2;
    }

    .loading {
        display: flex;
        flex-direction: column;
        align-items: center;

        .loading-logo {
            height: 6.4rem;
            margin-bottom: 2rem;

            .logo {
                @include size(6.4rem);
                justify-self: center;

                .logo-inner {
                    @include col(1, 0rem);

                    .logo-icon,
                    .logo-typo {
                        grid-column: 1/2;
                        grid-row: 1/2;
                        display: block;
                        @include size(100%)
                    }

                    .logo-typo {}

                    @include keyframes(rotating) {
                        from {
                            transform: rotateZ(0deg);
                        }

                        to {
                            transform: rotateZ(359deg);
                        }
                    }
                }
            }
        }

        .heading.--h3 {
            font-size: 4rem;
            color: $white30-cl;
            margin-bottom: 1rem;
        }

        .loading-txt-wrap {
            display: grid;
            margin-bottom: 2.8rem;

            .txt-item {
                font-family: 'ps';
                font-size: 6.6rem;
                line-height: 6rem;
                text-transform: uppercase;
                grid-column: 1/2;
                grid-row: 1/2;
                text-align: center;
                opacity: 0;
            }
        }

        .--t12 {
            color: $white30-cl;
            text-transform: uppercase;
        }
    }
}