bp-cursor {
    @include fullscreen(true);
    z-index: 888;
    pointer-events: none;
    --cursorSize: 3.2rem;
    --cursorSizeSm: .4rem;
    display: none;

    .cursor {
        @include size(var(--cursorSize));
        @include br(50%);
        position: absolute;
        background-color: rgba(104, 104, 104, .3);
        border: .2rem solid rgba(104, 104, 104, 0);
        top: calc((calc(var(--cursorSize) / 2)) * -1);
        left: calc(calc(var(--cursorSize) / 2) * -1);
        // transition: background-color $t linear, backdrop-filter .6s linear, -webkit-backdrop-filter .6s linear, border-color $t linear, width $t ease, height $t ease, top $t ease, left $t ease;
        // backdrop-filter: blur(20px);
        // -webkit-backdrop-filter: blur(20px);
        filter: blur(10px);
        -webkit-filter: blur(10px);
        //transition: background-color $t linear, filter .6s linear, -webkit-filter .6s linear, border-color $t linear, width $t ease, height $t ease, top $t ease, left $t ease;
        //@include transition('background-color $t linear, filter .6s linear, -webkit-filter .6s linear, border-color $t linear, width $t ease, height $t ease, top $t ease, left $t ease');
        transition: background-color $t linear, filter .6s linear, -webkit-filter .6s linear, border-color $t linear, width $t ease, height $t ease, top $t ease, left $t ease;

        // &.hover {
        //     @include size(calc(var(--cursorSize) * 3));
        //     background-color: rgba(104, 104, 104, .1);
        //     backdrop-filter: blur(1px);
        //     -webkit-backdrop-filter: blur(1px);
        //     border: .1rem solid rgba(104, 104, 104, .6);
        //     top: calc(calc(var(--cursorSize) * 3 / 2) * -1);
        //     left: calc(calc(var(--cursorSize) * 3 / 2) * -1)
        // }

        &.hover {
            @include size(calc(var(--cursorSize) * 3));
            background-color: rgba(255, 255, 255, .1);
            border: .1rem solid rgba(104, 104, 104, .6);
            top: calc(calc(var(--cursorSize) * 3 / 2) * -1);
            left: calc(calc(var(--cursorSize) * 3 / 2) * -1);
            filter: blur(20px);
            -webkit-filter: blur(20px);
        }
    }

    .cursor-inner {
        @include size(var(--cursorSizeSm));
        @include br(50%);
        position: absolute;
        background-color: $white-cl;
        top: calc((calc(var(--cursorSizeSm) / 2)) * -1);
        left: calc(calc(var(--cursorSizeSm) / 2) * -1);
        transition: background-color $t ease, height $t ease, top $t ease, left $t ease;

        // &.hover {
        //     @include size(calc(var(--cursorSizeSm) / 2));
        //     top: calc((calc(var(--cursorSizeSm) / 4)) * -1);
        //     left: calc(calc(var(--cursorSizeSm) / 4) * -1);
        // }
    }
}

.experience {
    @include fullscreen(true);
    z-index: 2;
    pointer-events: none;
}

.noise-bg {
    //display: none;
    @include fullscreen(100%);
    pointer-events: none;
    z-index: 999;

    &::before {
        content: '';
        @include size(calc(100% + 20rem));
        position: absolute;
        top: -10rem;
        left: -10rem;
        background-image: url('https://enchanting-tulumba-553b16.netlify.app/img/noise.png');
        animation: noise 1s steps(2) infinite;
        will-change: transform;
    }

    @keyframes noise {
        0% {
            transform: translate3d(0, 9rem, 0);
        }

        10% {
            transform: translate3d(-1rem, -4rem, 0);
        }

        20% {
            transform: translate3d(-8rem, 2rem, 0);
        }

        30% {
            transform: translate3d(9rem, -9rem, 0);
        }

        40% {
            transform: translate3d(-2rem, 7rem, 0);
        }

        50% {
            transform: translate3d(-9rem, -4rem, 0);
        }

        60% {
            transform: translate3d(2rem, 6rem, 0);
        }

        70% {
            transform: translate3d(7rem, -8rem, 0);
        }

        80% {
            transform: translate3d(-9rem, 1rem, 0);
        }

        90% {
            transform: translate3d(6rem, -5rem, 0);
        }

        to {
            transform: translate3d(-7rem, 0, 0);
        }
    }

}

.grad-bg {
    @include fullscreen(100%);
    pointer-events: none;
    z-index: 998;
    background: linear-gradient(180deg, rgba(14, 14, 14, 0) 60%, #0E0E0E 96%);
    opacity: 0;
}

.blur-bg {
    position: fixed;
    left: 50%;
    top: 50%;
    @include transform(translate(-50%, -50%));
    @include size(75vh);
    pointer-events: none;
    z-index: 1;
    background: radial-gradient(50% 50% at 50% 50%, #202020 0%, #0E0E0E 100%);
    opacity: 0;
}

