bp-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 777;

    .header {
        margin-top: 4rem;

        .container {
            align-items: center;
        }

        .name,
        .network,
        .contact,
        .time,
        .menu-toggle {
            display: flex;
            flex-direction: column;
            grid-row: 1/2;
            align-items: start;

            .--t14 {
                color: $white30-cl;
            }
        }

        .name {
            grid-column: 1/3;
        }

        .--txt-div {
            margin: 0 .8rem;
            color: $white10-cl;
        }

        .network {
            grid-column: 3/5;

            .socials {
                &>* {
                    display: inline-block;
                }
            }
        }

        .contact {
            grid-column: 5/7;
        }

        .logo {
            grid-column: 1/17;
            grid-row: 1/2;
            @include size(10.2rem);
            justify-self: center;
            display: grid;

            .logo-inner {
                @include col(1, 0rem);

                .logo-icon,
                .logo-typo {
                    grid-column: 1/2;
                    grid-row: 1/2;
                    display: block;
                    @include size(100%)
                }

                .logo-icon {
                    z-index: 2;
                }

                .logo-typo {}

                @include keyframes(rotating) {
                    from {
                        transform: rotateZ(0deg);
                    }

                    to {
                        transform: rotateZ(359deg);
                    }
                }
            }
        }

        .time.--sg {
            grid-column: 11/13;
        }

        .time.--vn {
            grid-column: 14/16;
        }

        .time {
            .time-inner>* {
                display: inline-block;
            }
        }

        .name,
        .network,
        .contact,
        .time,
        .menu-toggle {
            @include transition(opacity $t ease)
        }

        .menu-toggle {
            grid-column: 16/17;
            display: flex;
            flex-direction: column;

            .--t14 {
                margin-bottom: 2rem;
                color: $white30-cl;
                @include transition(color $t ease);
            }

            .menu-line {
                @include size(100%, .1rem);
                max-width: 5rem;
                background-color: $white30-cl;

                .menu-line-inner {
                    @include size(100%);
                    background-color: $white-cl;
                    @include transform(scaleX(0));
                    @include transition(transform $t $cubic);
                    transform-origin: 100% 50%;
                }
            }

            &:hover {
                .menu-line {
                    .menu-line-inner {
                        transform-origin: 0% 50%;
                        @include transform(scaleX(1));
                    }
                }
            }
        }

        &.before-loading {

            .name,
            .network,
            .contact,
            .time,
            .menu-toggle {
                //opacity: 0;
                opacity: 1;
            }
        }
    }
}